.expenses-filter {
    color: white;
    padding: 0 1rem;
    background-color: black;
    width: 96%;
    border-radius: 15px;
  }
  
  .expenses-filter__control {
    display: flex;
    width: 100%;
    align-items: center;
    justify-content: space-between;
    margin: 1rem 0;
  }
  
  .expenses-filter label {
    font-weight: bold;
    margin-bottom: 0.5rem;
  }
  
  .expenses-filter select {
    font: inherit;
    padding: 0.5rem 3rem;
    font-weight: bold;
    border-radius: 6px;
  }

  @media(max-width:790px){
    .expenses-filter {

      width: 90%;
    }
    
  }